import styled from 'styled-components';
import { useParams } from 'react-router-dom';
import useDocumentTitle from '../useDocumentTitle';
import { LinkStyled } from './LinkStyled';

const Wrapper = styled.div`
  margin: 1rem;
  & p,h1 {
    margin: .5rem;
  }
`;

const BattlesWrapper = styled.div`
  display: flex;
  flex-direction: column;
`;

export default function Participant(props) {
  const params = useParams();

  useDocumentTitle(`${params.id} - INGAME`);

  const participant = props.participants.find(e => e.userName === params.id);
  const participantBattles = props.battles.filter(e => e.participants.includes(participant.userName)) || [];

  return (
    <Wrapper>
      <h1>{participant.userName}</h1>
      <p>Ранг: {participant.userRank}</p>
      <p>Игры: {participant.userGames?.join(` | `) || `Участник не предоставил список игр.`}</p>
      <BattlesWrapper>
        <p>Батлы:</p>
        {
          participantBattles.length < 1 ?
            <p>Участник не принимал участия в батлах.</p> :
            participantBattles.map(e =>
              <LinkStyled to={`/battles/${e.battleName}`}>{e.battleName}</LinkStyled>
            )
        }
      </BattlesWrapper>
    </Wrapper>
  )
}