import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { battles } from '../objects';

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
`;

const ScheduleWrapper = styled.div`
  width: 30%;
  height:90vh;
  // background-color: lightgrey;
  overflow: auto;
  scrollbar-color: orange #d9d9d9;
`;

const PyramidWrapper = styled.div`
  text-align: center;
  overflow-y: auto;
  min-height: 50px;
  padding: 0 1em;
  width: 70%;
  box-sizing: border-box;
  border-left: 1px solid black;
`;

const PyramidRowWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid black;
`;

const ParticepantWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: safe center;
  flex-wrap: wrap;
  flex-grow: 1;
`;

const ParticepantLink = styled(Link)`
  box-sizing: border-box;
  background-color: white;
  padding: .5em;
  margin: .5em;
  border: .15em solid transparent;
  text-decoration: underline .1rem transparent;
  color: black;
  border-color: ${props => props.bordercolor || `black`};
  transition: all .25s ease;

  &:hover {
    box-sizing: border-box;
    cursor: pointer;
    text-decoration-color: #ff00aa;
  }

  &:active {
    color: black;
    background-color: grey;
  }

  &:visited {
    color: black;
  }
`;

const BattleCard = styled.div`
  box-sizing: border-box;
  background-color: ${props => props.backgroundcolor || `white`};
  border: 1px solid black;
  margin: .2em;
  padding: .15em;
`;


const LinkH2 = styled(Link)`
  font-weight: bold;
  font-size 24px;
  color: black;
  text-decoration: underline .1rem transparent;
  transition: all .25s ease;


  &:visited {
    color: black;
  }

  &:hover {
    text-decoration-color: #ff00aa;
  }
`;

const LinkH3 = styled(Link)`
  margin: 2px;
  white-space: nowrap;
  font-style: italic;
  font-size 16px;
  color: #2b2eed;
  text-decoration: underline .1rem transparent;
  transition: all .25s ease;

  &:hover {
    text-decoration-color: #ff00aa;
  }
`;

export default function Pyramid({participants}) {
  const participantsByRank = {};
 
  participants.filter(e => e.active === true)
  .forEach(p =>
    participantsByRank[p.userRank] ?
    participantsByRank[p.userRank].push(p) :
      participantsByRank[p.userRank] = [p]
    );

  const battlesActiveVote = battles.filter(e => (e.voteExpDate !== null) && (Date.now() < new Date(e.voteExpDate)))
    .sort((a, b) => new Date(a.voteExpDate) - new Date(b.voteExpDate));
  const battlesActiveBattle = battles.filter(e => (e.voteExpDate === null) && (Date.now() < new Date(e.battleExpDate)))
    .sort((a, b) => new Date(a.battleExpDate) - new Date(b.battleExpDate));

  const activeBattles = battlesActiveVote.concat(battlesActiveBattle);

  const participantsWithActiveBattles = new Set(activeBattles.map(e => e.participants).flat());
  const listRows = Object.keys(participantsByRank)
  .sort((a, b) => a-b)
  .map(rank =>
    <PyramidRowWrapper>
      <p>  
        {rank}
      </p>
      <ParticepantWrapper>
        {
          participantsByRank[rank].map(e =>
            <ParticepantLink bordercolor={participantsWithActiveBattles.has(e.userName) ? `red` : `green`} to={`/participants/${e.userName}`}>{e.userName}</ParticepantLink>
          )
        }
      </ParticepantWrapper>
    </PyramidRowWrapper>
  );

  function checkForFiles(battle, name) {
    try {
      require(`${battle.folderPath + name}/1.png`);
    }
    catch {
      return false;
    }

    return true;
  }

  function decideColor({participants, voteExpDate}, photosStatus) {
    const count = participants.reduce((acc, p) => acc + photosStatus[p], 0);

    const green = `#9bf08c`;
    const yellow = `#f0d98c`;
    const red = `#f08c8c`;

    if ((voteExpDate !== null) || (count === participants.length)) {
      return green;
    }

    if (count === 0) {
      return red;
    }

    if (count < participants.length) {
      return yellow;
    }
  }

  const activeBattlesCards = activeBattles.map(e => {
    const photosStatus = {};

    e.participants.forEach(p => {
      photosStatus[p] = checkForFiles(e, p);
    })

    return (
      <BattleCard backgroundcolor={decideColor(e, photosStatus)}>
        <LinkH2  to={`${`/battles/${e.battleName}`}`}>{e.battleName}</LinkH2>
        <h3>Участники:</h3>
        {e.participants.map(p =>
          <div>
            <LinkH3 to={`${`/participants/${p}`}`}>
              {
                `${p} (Работы ${photosStatus[p] ? `` : `не`} сданы)`
              }
            </LinkH3>
          </div>
        )}
        <h3>
          {
            e.voteExpDate ?
            `Дата окончания голосования:` :
            `Дата окончания приёма работ:`
          }
        </h3>
        <p>
          {
            e.voteExpDate ?
            new Date(e.voteExpDate).toString() :
            new Date(e.battleExpDate).toString()
          }
        </p>
      </BattleCard>
    );
  });

  return (
    <Wrapper>
      <ScheduleWrapper>
        {activeBattlesCards}
      </ScheduleWrapper>
      <PyramidWrapper>
        {listRows}
      </PyramidWrapper>
    </Wrapper>
  )
}