import styled from 'styled-components'
import useDocumentTitle from '../useDocumentTitle';
import { LinkStyled } from './LinkStyled';

const AllParticipantsWrapper = styled.div`
  display: flex;
  flex-direction: column;
  margin: 1rem;
`;

export default function Battle(props) {
  useDocumentTitle(`Все участники - INGAME`);
  const names = props.participant.map(e => e.userName).sort();

  return (
    <AllParticipantsWrapper>
      {
        names.length < 1 ?
          `Участников не обнаружено` :
          names.map(e => 
            <LinkStyled to={`/participants/${e}`}>{e}</LinkStyled>
          )
      }
    </AllParticipantsWrapper>
  );
}
