import styled from 'styled-components'
import { useEffect } from 'react'
import { useParams, Link } from 'react-router-dom';
import useDocumentTitle from '../useDocumentTitle';

const BattleWrapper = styled.div`
  margin: 1rem;
`
const Img = styled.img`
  height: 108px;
  width: 192px;
  margin: 1rem;
`;

const GameTitle = styled.h2`
  padding: .5rem;
  padding-left: 0;
  margin: 0;
  background-color: white;
`;


const LinkStyled = styled(Link)`
  color: #2b2eed;
  transition: all .25s ease;
  text-decoration: underline .1rem transparent;

  &:hover {
    text-decoration-color: #ff00aa;
  }
`;


const LinkAndImgsWrapper = styled.div`
  margin-top: .5rem;
  box-sizing: border-box;
  border-bottom: 1px solid black;
`;

const ImgsWrapper = styled.div`
  margin-top: .5rem;
`;

const Vote = styled.div`
  margin-top: .5rem;
`;

function getAllPhotos(participants, folderPath) {
  const obj = {};
  
  participants.forEach(p => {
    obj[p] = [];
    
    try {
      for (let i = 1; ; i++) {
        obj[p].push(<Img src={require(`${folderPath + p + `/${i}.png`}`)} alt={`${p} ${i}.png`}/>);
      }
    }
    finally {
      if (obj[p].length < 1) {
        obj[p] = ['Фото участника не найдены'];
      }

      return;
    }
  });
  
  return obj;
}

export default function Battle(props) {
  const params = useParams();
  const battle = props.battles.find(e => e.battleName === params.id);

  useEffect(() => {
    if (battle.voteExpDate === null) {
      return;
    }

    const script = document.createElement('script');

    script.src = battle.voteEmbed;
    script.async = true;
    script.setAttribute('data-telegram-post', battle.dataTelegram)

     document.getElementById('vote')?.appendChild(script);
  });

  useDocumentTitle(`${params.id} - INGAME`);

  return (
    <BattleWrapper>
      <GameTitle>
        {battle.game}
      </GameTitle>
      {
        Object.entries(getAllPhotos(battle.participants, battle.folderPath))
        .map(e => 
        <LinkAndImgsWrapper>
          <LinkStyled to={`/participants/${e[0]}`}>{e[0]}</LinkStyled>
          <ImgsWrapper>
            {[...e[1]]}
          </ImgsWrapper>
        </LinkAndImgsWrapper>
        )
      }

      <Vote id='vote'></Vote>
    </BattleWrapper>
  )
}
